import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import tw from 'twin.macro'
import * as Yup from 'yup'
import { Formik, Form, Field, ErrorMessage } from 'formik'
import { useHistory } from 'react-router-dom'
import { useGlobalContext } from '../context/context'

import PostCard from '../components/PostCard'
import Sidebar from '../components/Sidebar'
import Widget from '../components/Widget'
import Stories from '../components/Stories'
import Navbar from '../components/Navbar'

import { XIcon } from '@heroicons/react/solid'

const Home = () => {
  let history = useHistory()
  const { allPosts, createPost, user } = useGlobalContext()
  const [isPost, setIsPost] = useState(false)

  const initialValues = {
    title: '',
    postText: '',
  }

  const checkLogin = () => {
    if (!user.status) {
      history.push('/login')
    }
  }

  useEffect(() => {
    if (!user) {
      checkLogin()
    }
  }, [user])

  const validationSchema = Yup.object().shape({
    title: Yup.string().required('Title is required'),
    postText: Yup.string().required(),
  })

  const handleSubmit = (data) => {
    createPost(data)
    //setIsPost(false)
  }

  return (
    <MainContainer>
      <Navbar />
      <Sidebar />
      <PostComponent>
        <CreatePostCard>
          <PostImage>
            <img
              src='https://image.shutterstock.com/image-vector/blank-avatar-photo-place-holder-600w-1095249842.jpg'
              alt=''
            />
          </PostImage>
          <div onClick={() => setIsPost(true)} className='input-body'>
            What on yout mind?
          </div>
        </CreatePostCard>
        {allPosts.map((post, index) => {
          return <PostCard post={post} key={index} />
        })}
      </PostComponent>
      <Widget />
      {isPost && (
        <CreatePostBg>
          <CreatePostMainCard>
            <MainCardTitle>
              <h1>Create Post</h1>
              <XIconStyle onClick={() => setIsPost(false)} />
            </MainCardTitle>
            <Formik
              initialValues={initialValues}
              onSubmit={handleSubmit}
              validationSchema={validationSchema}
            >
              <Form className='flex flex-col p-4'>
                <InputField
                  id=''
                  name='title'
                  placeholder='Give a title'
                  className='text-2xl'
                />

                <InputField
                  id=''
                  type='textarea'
                  name='postText'
                  placeholder='What on your mind'
                  className='h-48 text-lg'
                />
                <ErrorMessage
                  className='text-red-400'
                  name='title'
                  component='span'
                />
                <ErrorMessage
                  className='text-red-400'
                  name='postText'
                  component='span'
                />
                <button type='submit' className='submit-btn'>
                  Post
                </button>
              </Form>
            </Formik>
          </CreatePostMainCard>
        </CreatePostBg>
      )}
    </MainContainer>
  )
}

const MainContainer = styled.div`
  overflow-y: hidden;

  ${tw`
      flex
      justify-between
      w-screen
      mt-10
      bg-gray-50     
    `}
`

const PostComponent = styled.div`
  ${tw`
      flex-grow
      h-screen
      max-w-2xl
      pt-8  
      overflow-y-auto
      pb-44
      xl:mr-16
      scrollbar-hide
  `}
`

const CreatePostCard = styled.div`
  ${tw`
    flex
    flex-row
    items-center
    justify-between
    h-20
    w-full
    my-4
    px-4
    py-2
    bg-white
    shadow-md
    rounded-md
    cursor-pointer
  `}

  .input-body {
    ${tw`
      flex-grow
      flex
      items-center
      w-8
      h-8
      mr-2
      md:h-12
      md:w-12
      px-4
      text-left
      bg-gray-50
      rounded-full
      cursor-pointer
    `}
  }
`

const PostImage = styled.div`
  img {
    height: 100%;
    width: 100%;
  }
  ${tw`w-8 h-8 mr-2 rounded-full md:h-12 md:w-12 overflow-hidden`}
`

const CreatePostBg = styled.div`
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 100;

  ${tw`
    px-4
    fixed
    top-0
    left-0
    right-0
    w-screen
    h-screen
    flex
    items-center
    justify-center
  `}
`

const CreatePostMainCard = styled.div`
  z-index: 110;

  ${tw`
    w-full
    md:max-w-lg
    bg-white
    rounded-lg
  `}

  .submit-btn {
    ${tw`
      py-2
      text-lg
      font-semibold
      text-white
      bg-indigo-500
      rounded-lg
      hover:bg-indigo-600
      hover:shadow-md
      transition
      duration-200
      ease-out
    `}
  }
`

const MainCardTitle = styled.div`
  ${tw`
    relative
    flex
    items-center
    justify-center
    py-4
    border-b
    border-gray-400
  `}

  h1 {
    ${tw`
      text-xl
      md:text-2xl
      font-semibold
      text-gray-800
    `}
  }
`

const XIconStyle = styled(XIcon)`
  ${tw`
    absolute
    top-3
    right-3
    p-1
    w-10
    h-10
    text-gray-700
    bg-gray-200
    rounded-full
    hover:bg-gray-300
    transition
    duration-200
    ease-out
  `}
`

const InputField = styled(Field)`
  ${tw`
    py-2
    mb-2
    focus:outline-none
  `}
`

export default Home
