import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import styled from 'styled-components'
import { useHistory } from 'react-router-dom'
import tw from 'twin.macro'
import { useGlobalContext } from '../context/context'

import { XIcon, DotsVerticalIcon } from '@heroicons/react/outline'

import Navbar from '../components/Navbar'

const SinglePost = () => {
  let history = useHistory()
  let { id } = useParams()
  const [newComment, setNewComment] = useState('')
  const [isActive, setIsActive] = useState(false)
  const { editPost } = useGlobalContext()

  const {
    singlePost,
    getSinglePosts,
    deletePost,
    postComments,
    createComments,
    deleteComment,
    authState,
  } = useGlobalContext()

  const commentInput = {
    commentBody: newComment,
    PostId: id,
  }

  useEffect(() => {
    getSinglePosts(id)
  }, [])

  const handleAddComments = () => {
    if (newComment.trim() === '') {
      console.log('Empty Input')
    } else {
      createComments(commentInput)
      setNewComment('')
    }
  }
  const handleDeletePost = (id) => {
    deletePost(id)
    history.push('/')
  }

  const handleDeleteComments = (id) => {
    deleteComment(id)
  }

  const handleEditPost = (option) => {
    if (option === 'title') {
      let newTitle = prompt('Enter New Title')
      let data = {
        type: 'title',
        newTitle,
        id,
      }
      editPost(data)
    } else if (option === 'body') {
      let newPostText = prompt('Enter New Text')
      let data = {
        type: 'body',
        newPostText,
        id,
      }
      editPost(data)
    }
  }

  return (
    <Container>
      <Navbar />
      <InnerContainer>
        <PostContainer>
          <UserContainer>
            <img
              className='w-14 h-14 rounded-full mr-4'
              src='https://image.shutterstock.com/image-vector/blank-avatar-photo-place-holder-600w-1095249842.jpg'
              alt=''
            />
            <p className='text-lg font-semibold'>{singlePost.username}</p>
            {authState.username === singlePost.username && (
              <IconContainer>
                <EditPostIconContainer
                  onClick={() => setIsActive(!isActive)}
                >
                  <PostEditIcon />
                </EditPostIconContainer>
                <EditPostIconContainer
                  onClick={() => handleDeletePost(singlePost.id)}
                >
                  <PostDeleteIcon />
                </EditPostIconContainer>
              </IconContainer>
            )}
          </UserContainer>
          <Title>{singlePost.title}</Title>
          <TextInfo>{singlePost.postText}</TextInfo>
          {isActive && (
            <EditListContainer>
              <h2 onClick={() => handleEditPost('title')}>Edit Title</h2>
              <h2 onClick={() => handleEditPost('body')}>Edit Text</h2>
            </EditListContainer>
          )}
        </PostContainer>
        <CommentContainer>
          <CommentInput>
            <input
              onChange={(e) => {
                setNewComment(e.target.value)
              }}
              value={newComment}
              type='text'
              placeholder='Comment...'
            />
            <CommentBtn onClick={handleAddComments}>Add Comment</CommentBtn>
          </CommentInput>
          <CommentListContainer>
            {postComments.map((comment, index) => {
              return (
                <div className='flex justify-center '>
                  <CommentAvater>{comment.username}</CommentAvater>
                  <CommentSingle key={index}>
                    <CommentText>{comment.commentBody}</CommentText>
                    {authState.username === comment.username && (
                      <XIcon
                        onClick={() => handleDeleteComments(comment.id)}
                        className='w-6 h-6 cursor-pointer text-gray-400 font-semibold'
                      />
                    )}
                  </CommentSingle>
                </div>
              )
            })}
          </CommentListContainer>
        </CommentContainer>
      </InnerContainer>
    </Container>
  )
}

const Container = styled.div`
  ${tw`
      flex
      flex-col
      justify-center
      items-center
      mt-10
    `}
`

const InnerContainer = styled.div`
  ${tw`
      mt-12
      w-3/4
      md:w-2/4
      xl:w-3/6
  `}
`

const PostContainer = styled.div`
  ${tw`
      relative
      w-full
      flex
      flex-col
      justify-items-start
      shadow-md
      rounded-lg
    `}
`

const UserContainer = styled.div`
  ${tw`
      relative
      flex
      items-center   
      pt-3
      pl-6
    `}
`

const Title = styled.div`
  ${tw`
    text-lg
    md:text-xl
    font-semibold
    px-4
    ml-20
    mb-1
 `}
`

const TextInfo = styled.div`
  ${tw`
    text-base
    md:text-lg
    px-4
    ml-20
    mb-2
 `}
`

const CommentContainer = styled.div`
  ${tw`
      w-full
      flex
      flex-col
      justify-center
      items-center
      pt-4
  `}
`

const CommentInput = styled.div`
  input {
    ${tw`
      w-2/3
      mr-2
      mb-3
      py-3
      px-4
      bg-gray-50
      rounded-full
      border-b-2
      border-gray-200
      outline-none
    `}
  }

  ${tw`
      flex
      w-full
  `}
`

const CommentBtn = styled.button`
  ${tw`
      max-h-12
      ml-auto
      text-sm
      text-white
      font-semibold
      bg-blue-400
      px-2
      rounded-full
  `}
`

const CommentListContainer = styled.div`
  ${tw`
      relative
      w-full
      border-t-2
      border-gray-100
      pt-4
  `}
`

const CommentAvater = styled.div`
  ${tw`
      flex
      justify-center
      items-center
      h-11
      w-11
      min-w-min
      mr-2
      bg-indigo-500
      text-white
      font-semibold
      rounded-full
  `}
`

const CommentSingle = styled.div`
  width: 90%;

  ${tw`
      flex
      justify-between
      items-center
      relative
      bg-gray-100
      min-h-full
      w-2/3
      px-2
      py-3
      mb-3
      rounded-xl
  `}
`

const CommentText = styled.p`
  overflow-wrap: break-word;
  word-wrap: break-word;
  hyphens: auto;
  width: 90%;
  ${tw`

  `}
`

const EditPostIconContainer = styled.div`
  ${tw`
    flex
    justify-center
    self-start
    mr-4
    mb-4
    p-2
    bg-gray-100
    rounded-lg
    cursor-pointer
    hover:bg-gray-200
  `}
`

const PostDeleteIcon = styled(XIcon)`
  ${tw`
    h-6
    w-6
    text-gray-400
    pointer-events-none
    rounded-full
  `}
`

const PostEditIcon = styled(DotsVerticalIcon)`
  ${tw`
    h-6
    w-6
    text-gray-400
    pointer-events-none
    rounded-full
  `}
`

const IconContainer = styled.div`
  right: 0;
  top: 0;

  ${tw`
    absolute
    flex
    items-center
    ml-auto
  `}
`

const EditListContainer = styled.div`
  ${tw`
      absolute
      right-4
      bottom-2
      flex
      flex-col
      justify-center
      items-center
      w-24
      h-20
      bg-white
      rounded-md
      shadow-md
  `}

  h2 {
    ${tw`
      py-1
      px-3
      rounded-md
      cursor-pointer
      hover:bg-gray-100
    `}
  }
`

export default SinglePost
