import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import tw from 'twin.macro'
import { useGlobalContext } from '../context/context'
import { useHistory } from 'react-router-dom'
import axios from 'axios'
import { Link } from 'react-router-dom'

const Login = () => {
  let history = useHistory()
  const { setAuthState, loginSetUser, user } = useGlobalContext()
  const [username, setUsername] = useState('')
  const [password, setPassword] = useState('')
  const [errorsObject, setErrorsObject] = useState([])

  const checkLogin = () => {
    if (user.status === true) {
      history.push('/')
    }
  }

  checkLogin()

  // useEffect(() => {
  //   checkLogin()
  // }, [])

  const loginData = {
    username: username,
    password: password,
  }

  const loginUser = async (data) => {
    try {
      const res = await axios.post(
        'https://full-stack-api-doggobook.herokuapp.com/auth/login',
        data
      )

      if (res.data) {
        localStorage.setItem('accessToken', res.data.token)
        loginSetUser({
          username: res.data.username,
          id: res.data.id,
          status: true,
        })
        setAuthState({
          username: res.data.username,
          id: res.data.id,
          status: true,
        })
        history.push('/')
      }
    } catch (err) {
      setErrorsObject(err.response.data)
      console.log(err.response.data)
    }
  }

  const handleLogin = () => {
    loginUser(loginData)
  }

  return (
    <SectionMain>
      <Container>
        <InnerContainer>
          <GreetContainer>
            <Title>DoggoBook</Title>
            <Info>
              Welcome to DoggoBook! Where we share memes not love. Laugh at hell
              joke are not gulity.
            </Info>
          </GreetContainer>
          <InputContainer>
            <Input
              type='text'
              placeholder='Username'
              onChange={(e) => {
                setUsername(e.target.value)
              }}
            />
            <Input
              type='password'
              placeholder='Password'
              onChange={(e) => {
                setPassword(e.target.value)
              }}
            />
            <Button onClick={handleLogin}>Login</Button>
            <RegisterText>
              <h3>
                Don't have a account?{' '}
                <Linktext to='/registration'>Register Here.</Linktext>
              </h3>
            </RegisterText>
          </InputContainer>
        </InnerContainer>
      </Container>
    </SectionMain>
  )
}

const SectionMain = styled.section`
  ${tw`
        h-screen
        w-screen
        bg-gray-100
    `}
`

const Container = styled.div`
  width: 90%;
  ${tw`
    m-auto
    flex
    justify-center
    items-center
  `}
`
const InnerContainer = styled.div`
  ${tw`
    flex
    flex-col
    justify-center
    items-center
    md:flex-row
    max-w-[900px]
    xl:min-w-[300px]
    p-4
    mt-8
    md:m-16
    overflow-x-hidden
  `}
`

const GreetContainer = styled.div`
  ${tw`
    flex
    flex-col
    w-full
    md:max-w-xl
    justify-center

  `}
`

const Title = styled.h1`
  ${tw`
    mb-5
    text-4xl
    font-bold
    text-center
    md:text-left
    text-indigo-500
    `}
`

const Info = styled.p`
  ${tw`
    text-xl
    font-semibold
    text-center
    md:text-left
    mb-10
    `}
`

const InputContainer = styled.div`
  ${tw`
    flex
    flex-col
    justify-start
    items-center
    w-96
    md:w-full
    md:min-w-max
    h-72
    p-3
    bg-white
    rounded-xl
  `}
`

const Input = styled.input`
  ${tw`
    w-full
    border-2
    border-gray-100
    text-xl
    px-3
    py-3
    mb-3
    rounded-xl
    outline-none
  `}
`

const Button = styled.button`
  ${tw`
    w-full
    px-2
    py-3
    mt-2
    bg-indigo-500
    rounded-xl
    text-white
    text-2xl
    font-semibold
  `}
`

const RegisterText = styled.div`
  ${tw`
    mt-2
  `}
`

const Linktext = styled(Link)`
  ${tw`
    text-indigo-400
    font-semibold
  `}
`
export default Login
