import React, { useState } from 'react'
import styled from 'styled-components'
import tw from 'twin.macro'
import { useHistory, Link } from 'react-router-dom'
import { useGlobalContext } from '../context/context'

import { HeartIcon, LinkIcon } from '@heroicons/react/solid'

const PostCard = ({ post }) => {
  const { likePost, likedPosts, allPosts } = useGlobalContext()
  const { title, postText, username, Likes, UserId } = post
  let history = useHistory()

  const handleLike = (data) => {
    //console.log(data)
    likePost(data)
  }

  //console.log(post)
  //console.log(likedPosts)

  return (
    <PostContainer>
      <PostHeader>
        <PostImage>
          <img
            src='https://image.shutterstock.com/image-vector/blank-avatar-photo-place-holder-600w-1095249842.jpg'
            alt=''
          />
        </PostImage>
        <PostName to={`/profile/${UserId}`}>{username}</PostName>
        <PostLikeContianer>
          <p className='mr-1'>{Likes ? `${Likes.length}` : '0'}</p>
          <PostLikeIconContainer
            onClick={() => handleLike({ PostId: post.id, UserId })}
          >
            <PostLikeIcon
              className={
                likedPosts.includes(post.id) ? 'text-red-400' : 'text-gray-300'
              }
            />
          </PostLikeIconContainer>
        </PostLikeContianer>
      </PostHeader>
      <PostInfo>
        <PostTitle
          onClick={() => {
            history.push(`/post/${post.id}`)
          }}
        >
          {title}
        </PostTitle>
        <PostBody
          onClick={() => {
            history.push(`/post/${post.id}`)
          }}
        >
          {postText}
        </PostBody>
      </PostInfo>
    </PostContainer>
  )
}

const PostContainer = styled.div`
  ${tw`
      flex
      flex-col
      mb-4
      rounded-lg
      cursor-pointer
      bg-white
      shadow-md
      hover:bg-gray-100
      transition
      duration-200
      ease-out
  `}
`
const PostHeader = styled.div`
  ${tw`flex items-center px-4 py-2 `}
`
const PostImage = styled.div`
  img {
    height: 100%;
    width: 100%;
  }
  ${tw`w-8 h-8 mr-2 rounded-full md:h-12 md:w-12 overflow-hidden`}
`

const PostName = styled(Link)`
  ${tw`font-semibold md:text-lg`}
`

const PostInfo = styled.div`
  ${tw`flex flex-col px-4 `}
`

const PostTitle = styled.div`
  ${tw`mb-1 text-xl font-semibold md:text-xl`}
`
const PostBody = styled.div`
  ${tw`text-base pb-2 md:text-lg`}
`

const PostLikeContianer = styled.div`
  ${tw`
      ml-auto
      flex
      justify-center
      items-center
  `}
`

const PostLikeIconContainer = styled.div`
  ${tw`
      
      flex
      justify-center
      items-center
      w-12
      h-12
      rounded-full
      hover:bg-gray-200
      cursor-pointer
  `}
`

const PostLikeIcon = styled(HeartIcon)`
  ${tw`
      h-6
      w-6
      pointer-events-none
  `}
`

export default PostCard
