import React from 'react'
import tw from 'twin.macro'
import styled from 'styled-components'
import { Link } from 'react-router-dom'

import { LinkIcon } from '@heroicons/react/outline'

import Navbar from '../components/Navbar'

const PageNotFound = () => {
  return (
    <Container>
      <Navbar />
      <InnerContainer>
        <IconContainer>
          <Icon />
        </IconContainer>
        <Title>Page Not Found</Title>
        <Info>
          Trying some tricky search huh? We are well protected from you. Get
          back to your meme.
        </Info>
        <LinkBtn to='/'>Back Home</LinkBtn>
      </InnerContainer>
    </Container>
  )
}

const Container = styled.div`
  ${tw`
    h-screen
    w-screen
    flex
    justify-center
    items-center
  `}
`

const InnerContainer = styled.div`
  ${tw`
    mx-8
    md:mx-0
    md:w-1/2
    lg:w-1/3
    flex
    flex-col
    justify-center
    items-center
  `}
`

const IconContainer = styled.div`
  ${tw`
    h-80
    w-80
    m-4
  `}
`

const Icon = styled(LinkIcon)`
  ${tw`
    h-full
    w-full
    text-red-600
  `}
`

const Title = styled.h1`
  ${tw`
    text-4xl
    text-center
    font-semibold
    p-2
    mb-2
  `}
`

const Info = styled.p`
  ${tw`
    text-xl
    text-center
    mb-3
  `}
`

const LinkBtn = styled(Link)`
  ${tw`
    mt-2
    py-2
    px-20
    md:py-3
    md:px-32
    text-lg
    text-center
    text-white
    bg-indigo-400
    rounded-3xl
  `}
`

export default PageNotFound
