import React, { useState } from 'react'
import styled from 'styled-components'
import tw from 'twin.macro'
import { useHistory } from 'react-router-dom'
import { useGlobalContext } from '../context/context'

import { KeyIcon } from '@heroicons/react/outline'

import Navbar from '../components/Navbar'

const EditProfile = () => {
  let history = useHistory()
  const { authState, editUser, error } = useGlobalContext()
  const [oldPassword, setOldPassword] = useState('')
  const [newPassword, setNewPassword] = useState('')
  const [rePassword, setRePassword] = useState('')
  const [isActive, setIsActive] = useState(false)

  const handleChange = () => {
    if (newPassword === oldPassword) {
      console.log('You are changing your old password as new password')
    } else if (newPassword != rePassword) {
      console.log("New Password does't match re-type password")
    } else {
      const editData = {
        oldPassword,
        newPassword,
      }
      console.log(editData)
      editUser(editData)
    }
  }

  return (
    <Container>
      <Navbar />
      <ProfileContainer>
        <ProfileCard>
          <CardTitle>Login</CardTitle>
          <CardTop className={isActive && 'border-b-2'}>
            <CardTopTitle>
              <KeyIcon className='w-6 h-6 text-gray-600' />
              <div className='ml-2'>
                <h2>Change password</h2>
                <p>
                  It's a good idea to use a strong password that you're not
                  using elsewhere
                </p>
              </div>
            </CardTopTitle>
            <CardTopBtn onClick={() => setIsActive(!isActive)}>Edit</CardTopBtn>
          </CardTop>
          {isActive && (
            <>
              <CardBody>
                <div className='flex flex-col items-end px-2'>
                  <LabelText className='mb-7' for='op'>
                    Current
                  </LabelText>
                  <LabelText className='mb-7' for='np'>
                    New Password
                  </LabelText>
                  <LabelText for='rp'>Re-type Password</LabelText>
                </div>
                <div className='flex flex-col px-2'>
                  <OldPassInput
                    id='op'
                    onChange={(e) => {
                      setOldPassword(e.target.value)
                    }}
                  />
                  <NewPassInput
                    id='np'
                    onChange={(e) => {
                      setNewPassword(e.target.value)
                    }}
                  />
                  <RePassInput
                    id='rp'
                    onChange={(e) => {
                      setRePassword(e.target.value)
                    }}
                  />
                </div>
              </CardBody>
              <BodyBtn onClick={handleChange}>Save Change</BodyBtn>
            </>
          )}
        </ProfileCard>
      </ProfileContainer>
    </Container>
  )
}

const Container = styled.div`
  ${tw`
    flex
    justify-center
    mt-20
  `}
`

const ProfileContainer = styled.div`
  width: 90%;

  ${tw`
    border-2
    max-w-3xl
    rounded-sm
  `}
`

const ProfileCard = styled.div`
  ${tw`
    flex
    flex-col
    items-center
  `}
`

const CardTitle = styled.div`
  ${tw`
    w-full
    flex
    items-center
    bg-gray-200
    py-3
    px-2
    font-semibold
  `}
`

const CardTop = styled.div`
  width: 95%;
  ${tw`
    flex
    justify-between
    items-center
    py-2
    px-3
    pr-4
  `}
`

const CardTopTitle = styled.div`
  ${tw`
    flex
    justify-between
    items-center
  `}

  p {
    ${tw`
        w-[19rem]
        md:w-full
      `}
  }
`

const CardTopBtn = styled.button`
  ${tw`
    py-1
    px-2
    font-semibold
    rounded-sm
    bg-gray-200
    hover:bg-gray-300
  `}
`

const CardBody = styled.div`
  ${tw`
    
    flex
    justify-center
    items-center
    pt-4
    pb-6
    border-b-2
  `}
`

const LabelText = styled.label`
  ${tw`
    font-semibold
  `}
`

const OldPassInput = styled.input`
  ${tw`
    outline-none
    border-2
    mb-6
    rounded-sm
  `}
`

const NewPassInput = styled.input`
  ${tw`
    outline-none
    border-2
    mb-6
    rounded-sm
  `}
`

const RePassInput = styled.input`
  ${tw`
    outline-none
    border-2
    rounded-sm
  `}
`

const BodyBtn = styled.button`
  ${tw`
    my-2
    bg-indigo-400
    text-white
    font-semibold
    py-1
    px-3
    rounded-sm
    hover:bg-indigo-500
  `}
`

export default EditProfile
