import React, { useState } from 'react'
import styled from 'styled-components'
import tw from 'twin.macro'
import { NavLink } from 'react-router-dom'

import {
  BellIcon,
  ChatIcon,
  ChevronDownIcon,
  HomeIcon,
  UserGroupIcon,
  ViewGridIcon,
  ArrowLeftIcon,
  UsersIcon,
} from '@heroicons/react/solid'
import {
  FlagIcon,
  PlayIcon,
  SearchIcon,
  ShoppingCartIcon,
} from '@heroicons/react/outline'
import DoggoLogo from '../image/doggo.jpg'

import data from '../dummyData/data'

import SearchPerson from './SearchPerson'
import RightFormList from './RightFormList'

const Navbar = () => {
  const [isActive, setIsActive] = useState(false)
  const [isRightActive, setIsRightActive] = useState(false)

  return (
    <NavContainer>
      <LeftContainer>
        {!isActive && (
          <ImageContainer>
            <img src={DoggoLogo} alt='' />
          </ImageContainer>
        )}
        <InputContainer>
          {isActive && (
            <LeftIcon
              onClick={() => setIsActive(!isActive)}
              className='cursor-pointer'
            />
          )}
          <InputBox
            className={`input ${isActive && 'active'}`}
            placeholder=''
            onClick={() => setIsActive(true)}
          />
          <InputSearch
            onClick={() => setIsActive(true)}
            className={`cursor-pointer search-icon ${isActive && 'active'}`}
          />
          {isActive && (
            <InputSearchDropBox>
              <PersonContainer>
                <InputSearchTitle>
                  <h1 className='text-lg font-semibold'>Recent Searches</h1>
                  <IEdit className='text-md'>Edit</IEdit>
                </InputSearchTitle>
                {data.map((item, index) => (
                  <SearchPerson key={index} item={item} />
                ))}
              </PersonContainer>
            </InputSearchDropBox>
          )}
        </InputContainer>
      </LeftContainer>
      <CenterContainer>
        <IconTest exact to='/'>
          <NavPageIcon activeClassName='active'>
            <HomeIcon />
          </NavPageIcon>
        </IconTest>
        <IconTest to='/create'>
          <NavPageIcon activeClassName='active'>
            <UsersIcon />
          </NavPageIcon>
        </IconTest>
        <IconTest to='/video'>
          <NavPageIcon activeClassName='active'>
            <PlayIcon />
          </NavPageIcon>
        </IconTest>
        <IconTest to='/market'>
          <NavPageIcon activeClassName='active'>
            <ShoppingCartIcon />
          </NavPageIcon>
        </IconTest>
        <IconTest to='/group'>
          <NavPageIcon activeClassName='active'>
            <UserGroupIcon />
          </NavPageIcon>
        </IconTest>
      </CenterContainer>
      <RightContainer>
        <NavRightIcon className='hidden md:flex'>
          <ViewGridIcon className='w-6 h-6' />
        </NavRightIcon>
        <NavRightIcon className='hidden md:flex'>
          <ChatIcon className='w-6 h-6' />
        </NavRightIcon>
        <NavRightIcon className='hidden md:flex'>
          <BellIcon className='w-6 h-6' />
        </NavRightIcon>
        <NavRightIcon
          onClick={() => setIsRightActive(!isRightActive)}
          className='flex'
        >
          <ChevronDownIcon className='w-6 h-6' />
        </NavRightIcon>
      </RightContainer>
      {isRightActive && (
        <FormContainer>
          <RightFormList />
        </FormContainer>
      )}
    </NavContainer>
  )
}

const NavContainer = styled.div`
  ${tw`
        fixed
        top-0
        left-0
        flex
        justify-between
        items-center
        w-screen
        h-16
        px-4
        border-b-2
        bg-white
        z-10
  `}
`

const LeftContainer = styled.div`
  ${tw`
        relative
        flex
        justify-center
        items-center
        pr-5
        md:pr-20
  `}
`

const InputContainer = styled.div`
  ${tw`
        relative
        flex
        justify-between
        items-center
        ml-2
  `}
`

const LeftIcon = styled(ArrowLeftIcon)`
  z-index: 12;
  ${tw`
    w-5
    h-5
    -ml-2
  `}
`

const InputSearch = styled(SearchIcon)`
  z-index: 12;
  ${tw`
        absolute
        left-3
        h-6
        w-6
        text-gray-400
  `}
`

const InputBox = styled.input`
  z-index: 12;
  ${tw`
        outline-none
        bg-gray-200
        w-12
        h-12
        pl-12
        md:py-2
        md:pl-10
        md:pr-2
        md:w-64
        rounded-3xl
  `}
`

const InputSearchTitle = styled.div`
  ${tw`
    flex
    justify-between
    items-center
    px-3
    `}
`

const IEdit = styled.h1`
  ${tw`
        cursor-pointer
        py-1 px-2
        rounded-md
        text-blue-500
        hover:bg-gray-200
    `}
`

const InputSearchDropBox = styled.div`
  z-index: 11;
  left: -1.5rem;
  top: -6px;

  @media screen and (max-width: 767px) {
    top: -19px;
  }

  ${tw`
    absolute
    w-80
    bg-gray-50
  `}
`

const PersonContainer = styled.div`
  ${tw`
    relative
    bg-gray-100
    w-full
    h-full
    pb-1
    pt-16
  `}
`

const ImageContainer = styled.div`
  ${tw`
        relative
        h-12
        w-12
        rounded-full
  `}

  img {
    width: 100%;
    height: 100%;
    overflow: hidden;
    border-radius: 100%;
  }
`

const CenterContainer = styled.div`
  ${tw`
    flex
    justify-between
    items-center
    h-full
    w-full
    max-w-xl
  `}
`

const IconTest = styled(NavLink)`
  ${tw`
      flex
      justify-center
      items-center
      h-full
      w-full
      hover:bg-gray-200
      hover:rounded-lg
    `}

  &.active {
    ${tw`
        text-blue-400
        border-b-4
        border-blue-400
      `}
  }
`

const NavPageIcon = styled.div`
  ${tw`
      w-6
      h-6
      md:w-8
      md:h-8
  `}
`

const RightContainer = styled.div`
  ${tw`
      flex
      pl-5
      md:pl-20
  `}
`

const NavRightIcon = styled.div`
  ${tw`
      w-8
      h-8
      mr-3
      md:w-10
      md:h-10
      justify-center
      items-center
      bg-gray-100
      rounded-full
      hover:bg-gray-200
  `}
`

const FormContainer = styled.div`
  ${tw`
      absolute
      right-4
      top-16
      shadow-2xl
      rounded-xl
  `}
`

export default Navbar
