import React from 'react'
import tw from 'twin.macro'
import styled from 'styled-components'

import { XIcon } from '@heroicons/react/solid'

const SearchPerson = ({ item }) => {
  const { name, src } = item

  return (
    <Container>
      <PersonContainer>
        <PersonImage>
          <img src={src} alt='' />
        </PersonImage>
        <h3>{name}</h3>
      </PersonContainer>
      <IconContainer>
        <Icon />
      </IconContainer>
    </Container>
  )
}

const Container = styled.div`
  ${tw`
    flex
    items-center
    justify-between
    px-3
    py-2
    rounded-md
    hover:bg-gray-200
  `}
`

const PersonContainer = styled.div`
  ${tw`
    flex
    items-center
  `}
`

const PersonImage = styled.div`
  ${tw`
    h-10
    w-10
    mr-3
  `}

  img {
    width: 100%;
    height: 100%;
    border-radius: 100%;
  }
`

const IconContainer = styled.div`
  ${tw`
        flex
        justify-center
        items-center
        w-7
        h-7
        rounded-full
        hover:bg-gray-300
    `}
`

const Icon = styled(XIcon)`
  ${tw`
        w-4
        h-4
        text-gray-500
    `}
`

export default SearchPerson
