import React, { useEffect, useState } from 'react'
import axios from 'axios'
import styled from 'styled-components'
import tw from 'twin.macro'
import { useHistory, useParams } from 'react-router-dom'
import { useGlobalContext } from '../context/context'

import {
  ChatIcon,
  PhoneIcon,
  UserRemoveIcon,
  DotsHorizontalIcon,
  PencilIcon,
  SearchIcon,
  ChevronDownIcon,
} from '@heroicons/react/solid'
import bgImg from '../image/highway.jpg'
import userImg from '../image/doggo.jpg'

import Navbar from '../components/Navbar'
import PostCard from '../components/PostCard'

const Profile = () => {
  let { id } = useParams()
  const [userProfile, setUserProfile] = useState('')
  const [listPosts, setListPosts] = useState([])
  const [isActive, setIsActive] = useState('Post')
  const { getUserPost, userPost } = useGlobalContext()

  useEffect(() => {
    axios
      .get(`https://full-stack-api-doggobook.herokuapp.com/auth/userinfo/${id}`)
      .then((res) => {
        setUserProfile(res.data)
      })

    // axios.get(`http://localhost:5000/posts/byuserId/${id}`).then((res) => {
    //   setListPosts(res.data)
    // })
    getUserPost(id)
  }, [])

  console.log(userPost)

  const { username } = userProfile
  const arrInfoItem = ['Post', 'About', 'Friend', 'Picture']

  const handleClick = (e) => {
    setIsActive(e.target.value)
  }

  return (
    <div className='flex flex-col justify-center items-center'>
      <Container>
        <Navbar />
        <TopContainer>
          <TopInnerContainer>
            <BgImg src={bgImg} />
            <ProfileAbsolute>
              <ProfileImg src={userImg} />
              <ProfileName>{username}</ProfileName>
            </ProfileAbsolute>
            <ProfileInfoContianer>
              <ProfileInfoLeft>
                {arrInfoItem.map((item, index) => (
                  <InfoItem
                    onClick={(e) => handleClick(e)}
                    className={`hidden md:flex ${
                      isActive === item
                        ? 'border-b-4 border-indigo-500 rounded-none'
                        : 'rounded-md hover:bg-gray-200'
                    }`}
                    value={item}
                    key={index}
                  >
                    {item}
                  </InfoItem>
                ))}
                <InfoItem className='flex justify-center items-center rounded-md hover:bg-gray-200'>
                  More
                  <InfoMoreIcon />
                </InfoItem>
              </ProfileInfoLeft>
              <ProfileInfoRight>
                <RightIconContainer>
                  <RightIcon>
                    <RightDyaIcon>
                      <UserRemoveIcon className='w-full h-full text-gray-600 pointer-events-none' />
                    </RightDyaIcon>
                    <p>Add Friend</p>
                  </RightIcon>
                  <RightIcon>
                    <ChatIcon className='w-6 h-6 text-gray-600 pointer-events-none' />
                  </RightIcon>
                  <RightIcon>
                    <SearchIcon className='w-6 h-6 text-gray-600 pointer-events-none' />
                  </RightIcon>
                  <RightIcon className='mr-2'>
                    <DotsHorizontalIcon className='w-6 h-6 text-gray-600 pointer-events-none' />
                  </RightIcon>
                </RightIconContainer>
              </ProfileInfoRight>
            </ProfileInfoContianer>
          </TopInnerContainer>
        </TopContainer>
      </Container>
      <FeedContianer>
        {userPost.map((post) => (
          <PostCard post={post} key={post.id} />
        ))}
      </FeedContianer>
    </div>
  )
}

const Container = styled.div`
  ${tw`
    flex
    flex-col
    justify-center
    items-center
    w-screen
    mt-16
  `}
`

const TopContainer = styled.div`
  ${tw`
    h-[21rem]
    md:h-[32rem]
    max-h-[32rem]
    w-full
    flex
    justify-center
    bg-gray-100
  `};
`

const TopInnerContainer = styled.div`
  ${tw`
    relative
    h-[55%]
    md:h-[67%]
    w-full
    max-w-[59rem]
  `}
`

const BgImg = styled.img`
  ${tw`
    h-full
    w-full
    max-h-80
    object-cover
  `}
`

const ProfileAbsolute = styled.div`
  ${tw`
      -mt-44
      flex
      flex-col
      justify-center
      items-center
  `}
`

const ProfileImg = styled.img`
  ${tw`
    h-44
    w-44
    md:h-48
    md:w-48
    border-4
    border-black
    rounded-full
  `}
`

const ProfileName = styled.h1`
  ${tw`
    text-3xl
    font-bold
  `}
`

const ProfileInfoContianer = styled.div`
  ${tw`
    mt-14
    h-16
    flex
    justify-between
    items-center
    border-t-2
    border-gray-300
  `}
`

const ProfileInfoLeft = styled.div`
  ${tw`
    flex
    justify-center
    items-center
  `}
`

const InfoItem = styled.button`
  ${tw`
    py-[0.85rem]
    px-3
    text-gray-700
    font-semibold
    cursor-pointer
  `}
`

const InfoMoreIcon = styled(ChevronDownIcon)`
  ${tw`
    mt-1
    h-6
    w-6
  `}
`

const ProfileInfoRight = styled.div`
  ${tw`
    flex
    justify-center
    items-center
  `}
`

const RightIconContainer = styled.div`
  ${tw`
    flex
    justify-center
    items-center
    h-full
    w-full
  `}
`

const RightDyaIcon = styled.div`
  ${tw`
    w-6
    h-6
    mr-2
  `}
`

const RightIcon = styled.div`
  p {
    ${tw`
      font-semibold
    `}
  }

  ${tw`
    flex
    justify-center
    items-center
    py-[0.45rem]
    px-3
    bg-gray-200
    rounded-md
    ml-2
  `}
`

const FeedContianer = styled.div`
  ${tw`
    mt-4
    w-full
    flex
    flex-col
    justify-center
    max-w-[60rem]
    scrollbar-hide
  `}
`

const UserPost = styled.div`
  ${tw`
    
  `}
`

export default Profile
