import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import { Formik, Form, Field, ErrorMessage } from 'formik'
import * as Yup from 'yup'
import styled from 'styled-components'
import tw from 'twin.macro'
import { useGlobalContext } from '../context/context'
import { useHistory } from 'react-router-dom'
import axios from 'axios'

const Register = () => {
  let history = useHistory()
  //const { registerUser } = useGlobalContext()
  const [errorsObject, setErrorsObject] = useState([])

  const initialValues = {
    username: '',
    password: '',
  }

  const validationSchema = Yup.object().shape({
    username: Yup.string().min(3).max(15).required(),
    password: Yup.string().min(4).max(20).required(),
  })

  const registerUser = async (data) => {
    try {
      setErrorsObject([])
      const res = await axios.post('https://full-stack-api-doggobook.herokuapp.com/auth', data)

      if (res.data) {
        history.push('/login')
      }
    } catch (err) {
      setErrorsObject(err.response.data.error)
    }
  }

  const handleRegister = (data) => {
    console.log(data);
    registerUser(data)
  }

  return (
    <SectionMain>
      <Title>Register Account</Title>
      <CreatePostContainer>
        <LayerContainer>
          <Formik
            initialValues={initialValues}
            onSubmit={handleRegister}
            validationSchema={validationSchema}
          >
            <Form className='flex flex-col p-4'>
              <label>Username: </label>
              <ErrorMessage name='username' component='span' />
              <Field
                id=''
                name='username'
                placeholder='User name'
                className='mb-4 border-2 border-gray-300 p-2'
              />
              <label>Password: </label>
              <ErrorMessage name='password' component='span' />
              <Field
                id=''
                name='password'
                type='password'
                placeholder='Password'
                className='mb-4 border-2 border-gray-300 p-2'
              />
              <Button type='submit'>Register</Button>
              <RegisterText>
                <h3>
                  Already have a account?{' '}
                  <Linktext to='/login'>Login Here.</Linktext>
                </h3>
              </RegisterText>
            </Form>
          </Formik>
        </LayerContainer>
      </CreatePostContainer>
    </SectionMain>
  )
}

const SectionMain = styled.section`
  ${tw`
        h-screen
        w-screen
        bg-gray-100
        flex
        flex-col
        items-center
    `}
`
const Title = styled.h1`
  ${tw`
    mb-5
    mt-10
    text-4xl
    font-bold
    text-center
    md:text-left
    text-indigo-500
    `}
`

const CreatePostContainer = styled.div`
  ${tw`
        w-96
        h-80
        bg-white
        border-2
        rounded-xl 
    `}
`

const LayerContainer = styled.div`
  ${tw`
        w-full    
        max-w-screen-md
        border-gray-300
        py-2
    `}
`

const Button = styled.button`
  ${tw`
    w-full
    px-2
    py-3
    mt-2
    bg-indigo-500
    rounded-xl
    text-white
    text-2xl
    font-semibold
  `}
`

const RegisterText = styled.div`
  ${tw`
    mt-2
  `}
`

const Linktext = styled(Link)`
  ${tw`
    text-indigo-400
    font-semibold
  `}
`

export default Register
