import React from 'react'
import styled from 'styled-components'
import tw from 'twin.macro'

const StoryCard = ({ name, src, profile }) => {
  return (
    <ImgContainer>
      <ImgSmall src={profile}/>
      <Imglarge src={src} />
    </ImgContainer>
  )
}
const ImgContainer = styled.div`
  ${tw`
        relative
        h-14
        w-14
        md:h-20
        md:w-20
        lg:h-56
        lg:w-32
        cursor-pointer
        p-3
        transition
        duration-200
        transform
        ease-in
        hover:scale-105
        hover:animate-pulse
    `}
`

const ImgSmall = styled.img`
  ${tw`
        max-h-10
        w-10
        absolute
        opacity-0
        lg:opacity-100
        rounded-full
        z-50
        top-4
        left-4
        object-cover
    `}
`

const Imglarge = styled.img`
  ${tw`
        h-8
        sm:h-14
        lg:h-56
        object-cover
        filter
        brightness-75
        lg:rounded-3xl
        rounded-full
        object-cover
    `}
`

export default StoryCard
