const reducer = (state, action) => {
  switch (action.type) {
    case 'LOGIN_USER':
      return {
        ...state,
        user: action.payload,
      }
    case 'LOGOUT_USER':
      return {
        ...state,
        user: { status: false },
      }
    case 'GET_POSTS':
      return {
        ...state,
        allPosts: action.payload.listOfPosts,
        likedPosts: action.payload.likedPosts.map((like) => {
          return like.PostId
        }),
      }
    case 'GET_POST':
      return {
        ...state,
        singlePost: action.payload,
      }
    case 'ADD_POST':
      return {
        ...state,
        allPosts: [...state.allPosts, action.payload],
      }
    case 'DELETE_POST':
      return {
        ...state,
        allPosts: state.allPosts.filter((post) => post.id !== action.payload),
      }
    case 'GET_COMMENTS':
      return {
        ...state,
        postComments: action.payload,
      }
    case 'ADD_COMMENT':
      return {
        ...state,
        postComments: [...state.postComments, action.payload],
      }
    case 'DELETE_COMMENT':
      return {
        ...state,
        postComments: state.postComments.filter((commentId) => {
          return commentId.id != action.payload
        }),
      }
    case 'LIKE_POST':
      return {
        ...state,
        likedPosts: state.likedPosts.filter((like) => {
          return like != action.payload
        }),
      }
    case 'UPDATE_POST_TITLE':
      return {
        ...state,
        singlePost: { ...state.singlePost, title: action.payload },
      }
    case 'UPDATE_POST_TEXT':
      return {
        ...state,
        singlePost: { ...state.singlePost, postText: action.payload },
      }
    case 'USER_POSTS':
      return {
        ...state,
        userPost: action.payload,
      }
    case 'ERROR':
      return {
        ...state,
        error: [action.payload],
      }
  }
}

export default reducer
