import React, {
  useState,
  useContext,
  useReducer,
  useEffect,
  useCallback,
} from 'react'
import axios from 'axios'
import reducer from './reducer'

const AppContext = React.createContext()

const initialState = {
  allPosts: [],
  likedPosts: [],
  singlePost: {},
  postComments: [],
  userPost: [],
  user: '',
  error: [],
}

const AppProvider = ({ children }) => {
  const [state, dispatch] = useReducer(reducer, initialState)
  const [posts, setPosts] = useState()
  const [authState, setAuthState] = useState({
    username: '',
    id: 0,
    state: false,
  })

  const logout = () => {
    localStorage.removeItem('accessToken')
    dispatch({ type: 'LOGOUT_USER' })
    setAuthState({
      username: '',
      id: 0,
      status: false,
    })
  }

  const loginSetUser = (data) => {
    dispatch({ type: 'LOGIN_USER', payload: data })
  }

  const editUser = async (data) => {
    try {
      const res = await axios.put(
        'https://full-stack-api-doggobook.herokuapp.com/auth/changepassword',
        {
          oldPassword: data.oldPassword,
          newPassword: data.newPassword,
        },
        {
          headers: { accessToken: localStorage.getItem('accessToken') },
        }
      )

      if (res.data.error) {
        dispatch({ type: 'ERROR', payload: res.data })
      }

      // axios
      //   .put(
      //     'http://localhost:5000/auth/changepassword',
      //     {
      //       oldPassword: data.oldPassword,
      //       newPassword: data.newPassword,
      //     },
      //     {
      //       headers: { accessToken: localStorage.getItem('accessToken') },
      //     }
      //   )
      //   .then((res) => {
      //     console.log(res.data)
      //   })
    } catch (err) {
      dispatch({ type: 'ERROR', payload: err.response.data })
    }
  }

  const getAllPosts = useCallback(async () => {
    try {
      const res = await axios(
        'https://full-stack-api-doggobook.herokuapp.com/posts',
        {
          headers: { accessToken: localStorage.getItem('accessToken') },
        }
      )
      const data = await res.data

      if (data) {
        setPosts(data)
        dispatch({ type: 'GET_POSTS', payload: data })
      }
    } catch (err) {
      dispatch({ type: 'ERROR', payload: err.response.data })
    }
  }, [])

  const getSinglePosts = useCallback(async (id) => {
    try {
      const res = await axios(
        `https://full-stack-api-doggobook.herokuapp.com/posts/byId/${id}`
      )
      const data = await res.data

      if (data) {
        setPosts(data)
        getComments(id)
        dispatch({ type: 'GET_POST', payload: data })
      }
    } catch (err) {
      dispatch({ type: 'ERROR', payload: err.response.data })
    }
  }, [])

  const createPost = (data) => {
    try {
      const res = axios
        .post('https://full-stack-api-doggobook.herokuapp.com/posts', data, {
          headers: { accessToken: localStorage.getItem('accessToken') },
        })
        .then((res) => {
          dispatch({ type: 'ADD_POST', payload: res.data })
        })
    } catch (err) {
      dispatch({ type: 'ERROR', payload: err.response.data })
    }
  }

  const deletePost = (id) => {
    try {
      axios
        .delete(`https://full-stack-api-doggobook.herokuapp.com/posts/${id}`, {
          headers: { accessToken: localStorage.getItem('accessToken') },
        })
        .then(() => {
          dispatch({ type: 'DELETE_POST', payload: id })
        })
    } catch (err) {
      dispatch({ type: 'ERROR', payload: err.response.data })
    }
  }

  const editPost = (data) => {
    try {
      if (data.type === 'title') {
        axios
          .put(
            `https://full-stack-api-doggobook.herokuapp.com/posts/title`,
            { newTitle: data.newTitle, id: data.id },
            {
              headers: { accessToken: localStorage.getItem('accessToken') },
            }
          )
          .then(() => {
            dispatch({ type: 'UPDATE_POST_TITLE', payload: data.newTitle })
          })
      } else if (data.type === 'body') {
        axios
          .put(
            `https://full-stack-api-doggobook.herokuapp.com/posts/postText`,
            { newText: data.newPostText, id: data.id },
            {
              headers: { accessToken: localStorage.getItem('accessToken') },
            }
          )
          .then(() => {
            dispatch({ type: 'UPDATE_POST_TEXT', payload: data.newPostText })
          })
      }
    } catch (err) {
      dispatch({ type: 'ERROR', payload: err.response.data })
    }
  }

  const likePost = (data) => {
    try {
      axios
        .post(
          `https://full-stack-api-doggobook.herokuapp.com/likes`,
          { PostId: data.PostId },
          { headers: { accessToken: localStorage.getItem('accessToken') } }
        )
        .then((res) => {
          getAllPosts()
          getUserPost(data.UserId)
          dispatch({ type: 'LIKE_POST', payload: data.PostId })
        })
    } catch (err) {
      dispatch({ type: 'ERROR', payload: err.response.data })
    }
  }

  const getComments = (id) => {
    try {
      const res = axios
        .get(`https://full-stack-api-doggobook.herokuapp.com/comments/${id}`)
        .then((res) => {
          dispatch({ type: 'GET_COMMENTS', payload: res.data })
        })

      //const data = await res.data
    } catch (err) {
      dispatch({ type: 'ERROR', payload: err.response.data })
    }
  }

  const createComments = (data) => {
    try {
      const res = axios
        .post('https://full-stack-api-doggobook.herokuapp.com/comments', data, {
          headers: { accessToken: localStorage.getItem('accessToken') },
        })
        .then((res) => {
          if (res.data.error) {
            alert(res.data.error)
          } else {
            dispatch({ type: 'ADD_COMMENT', payload: res.data })
          }
        })
    } catch (err) {
      dispatch({ type: 'ERROR', payload: err.response.data })
    }
  }

  const deleteComment = (id) => {
    try {
      const res = axios
        .delete(
          `https://full-stack-api-doggobook.herokuapp.com/comments/${id}`,
          {
            headers: { accessToken: localStorage.getItem('accessToken') },
          }
        )
        .then((res) => {
          if (res.data.error) {
            alert(res.data.error)
          } else {
            console.log(res.data)
            dispatch({ type: 'DELETE_COMMENT', payload: id })
          }
        })
    } catch (err) {
      dispatch({ type: 'ERROR', payload: err.response.data })
    }
  }

  const getUserPost = (id) => {
    try {
      axios
        .get(
          `https://full-stack-api-doggobook.herokuapp.com/posts/byuserId/${id}`
        )
        .then((res) => {
          dispatch({ type: 'USER_POSTS', payload: res.data })
        })
    } catch (err) {
      dispatch({ type: 'ERROR', payload: err.response.data })
    }
  }

  // const registerUser = async (data) => {
  //   try {
  //     setErrorsObject([])
  //     const res = await axios.post('http://localhost:5000/auth', data)
  //   } catch (err) {
  //     setErrorsObject(err.response.data.error)
  //   }
  // }

  // const loginUser = async (data) => {
  //   try {
  //     setErrorsObject([])
  //     const res = await axios.post('http://localhost:5000/auth/login', data)

  //     if (res.data) {
  //       sessionStorage.setItem('accessToken', res.data)
  //     }
  //   } catch (err) {
  //     setErrorsObject(err.response.data.error)
  //   }
  // }

  const checkLocalToken = async () => {
    try {
      const res = await axios.get(
        'https://full-stack-api-doggobook.herokuapp.com/auth/auth',
        {
          headers: { accessToken: localStorage.getItem('accessToken') },
        }
      )

      if (res.data.error) {
        setAuthState({ ...authState, status: 'false' })
      } else {
        const data = {
          username: res.data.username,
          id: res.data.id,
          status: true,
        }
        dispatch({ type: 'LOGIN_USER', payload: data })
        setAuthState({
          username: res.data.username,
          id: res.data.id,
          status: true,
        })
      }
    } catch (err) {
      console.log(err.response.data.error)
    }
  }

  useEffect(() => {
    getAllPosts()
    checkLocalToken()
  }, [])

  return (
    <AppContext.Provider
      value={{
        ...state,
        posts,
        createPost,
        editPost,
        deletePost,
        likePost,
        getSinglePosts,
        getAllPosts,
        getComments,
        createComments,
        deleteComment,
        setAuthState,
        authState,
        loginSetUser,
        logout,
        editUser,
        checkLocalToken,
        getUserPost,
      }}
    >
      {children}
    </AppContext.Provider>
  )
}

export const useGlobalContext = () => {
  return useContext(AppContext)
}

export { AppContext, AppProvider }
