import React from 'react'
import styled from 'styled-components'
import tw from 'twin.macro'
import { useHistory } from 'react-router-dom'
import { useGlobalContext } from '../context/context'

import { LogoutIcon, ChevronRightIcon, CogIcon } from '@heroicons/react/solid'

const RightFormList = () => {
  let history = useHistory()
  const { authState, logout, setAuthState } = useGlobalContext()

  const handleLogout = () => {
    history.push('/login')
    logout()
    setAuthState(false)
  }

  return (
    <Container>
      <TopProfileContainer>
        <ProfileImg></ProfileImg>
        <div className='ml-4'>
          <ProfileName>{authState.username}</ProfileName>
          <p>Check your profile</p>
        </div>
      </TopProfileContainer>
      <IconContainer>
        <IndividualIcon
          onClick={() => {
            history.push('/editprofile')
          }}
        >
          <IconList>
            <CogIcon className='w-6 h-6' />
          </IconList>
          <IconListInfo>
            <div className='font-semibold pointer-events-none'>
              Edit Profile
            </div>
            <ChevronRightIcon className='w-8 h-8' />
          </IconListInfo>
        </IndividualIcon>
        <IndividualIcon onClick={handleLogout}>
          <IconList>
            <Icon />
          </IconList>
          <IconListInfo>
            <div className='font-semibold pointer-events-none'>Logout</div>
          </IconListInfo>
        </IndividualIcon>
      </IconContainer>
    </Container>
  )
}

const Container = styled.div`
  ${tw`
    flex
    flex-col
    justify-center
    p-3
    w-80
    bg-gray-100
    rounded-md
  `}
`

const TopProfileContainer = styled.div`
  ${tw`
    flex
    items-center
    pb-4
  `}
`

const ProfileImg = styled.div`
  ${tw`
    w-16
    h-16
    rounded-full
    bg-gray-200
  `}
`

const ProfileName = styled.div`
  ${tw`
    text-xl
    font-semibold
  `}
`

const IconContainer = styled.div`
  ${tw`
    flex
    flex-col
    items-center
    w-full
    pt-2
    border-t-2
  `}
`

const IndividualIcon = styled.div`
  ${tw`
    flex
    items-center
    w-full
    hover:bg-gray-200
    py-2
    px-2
    rounded-md
    `}
`

const IconList = styled.div`
  ${tw`
    w-10
    h-10
    mr-4
    flex
    justify-center
    items-center
    bg-gray-300
    rounded-full
  `}
`

const Icon = styled(LogoutIcon)`
  ${tw`
    w-6
    h-6
    text-black
  `}
`

const IconListInfo = styled.div`
  ${tw`
    flex
    flex-grow
    justify-between
    items-center
  `}
`

export default RightFormList
